.work {
    display: flex;
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.work-filter {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 4rem 0 2rem;

    .work-filter-item {
        padding: .5rem 1rem;
        border-radius: .2rem;
        background: var(--white-color);
        color: var(--black-color);
        font-weight: 800;
        cursor: pointer;
        margin: .5rem;
        transition: all 0.5s ease;

        &:hover {
            background: var(--secondary-color);
            color: var(--white-color);
        }

        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
            border-radius: .85rem;
        }
    }

    .item-active {
        background: var(--secondary-color);
            color: var(--white-color);
    }

}
.work-portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .work-item {
        display: flex;
        width: 280px;
        flex-direction: column;
        margin: 2rem;
        padding: 1rem;
        border-radius: 0.3rem;
        background: var(--white-color);
        color: var(--black-color);
        cursor: pointer;
        transition: all .5s ease;

        &:hover {
            box-shadow: 0 0 25px var(--gray-color);
        }

        @media screen and (min-width: 2000px) {
            width: 470px;
            padding: 1.25rem;
            border-radius: .7rem;
        }

        @media screen and (max-width: 400px) {
            width: 100%;
           margin: 1rem;
        }

        .work-img {
            width: 100%;
            height: 240px;
            position: relative;

            img {
                width: 100%;
                height: 100%;
                border-radius: .3rem;
                object-fit: cover;
            }

            @media screen and (min-width: 2000px) {
                height: 280px;
            }

            .work-hover {
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background: rgba(0, 0, 0, 0.2);
                border-radius: .3rem;
                opacity: 0;
                transition: all 0.2s ease;

                div {
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    background: rgba(0, 0, 0, 0.7);
                    margin: 1rem;
                    color: var(--white-color);
                    font-weight: 800;
                    font-family: var(--font-base);

                    svg {
                        width: 50%;
                        height: 50%;
                        color: var(--lightGray-color);
                    }
                }
            }
        }

        .work-content {
            padding: .5rem;
            width: 100%;
            position: relative;
            flex-direction: column;

            h4 {
                line-height: 1.5;
                margin-top: 1rem;
                // text-align: center;
            }

            .work-tag {
                position: absolute;
                padding: .5rem 1rem;
                border-radius: 10px;
                // border: 2px solid var(--gray-color);
                // border-bottom: none;
                z-index: 0;
                background: var(--white-color);
                top: -25px;
            }
        }
    }
}