#home {
    position: relative;
    // background: url('../../assets/bg-light.png');
    background: var(--white-color);
    background-size: cover;
    background-repeat: repeat;
    background-position: center;

    .wrapper {
        padding: 0;

        .copyright {
            display: none;
        }
    }
}

// #home {
//     background: linear-gradient(rgba(0, 0, 0, 0.8),
//       rgba(0, 0, 0, 0.99)),url('../../assets/bgIMG.png') !important;
// }

.header {
    flex: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    padding: 3rem 2rem 0;
    background-size: cover;

    @media screen and (min-width: 2000px) {
        padding-top: 8rem;
    }
    @media screen and (max-width: 1200px) {
        flex-direction: column;
    }
    @media screen and (max-width: 400px) {
        padding: 6rem 1rem 2rem;
    }
}

.header-info {
    flex: 0.65;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
    margin: auto;

    @media screen and (max-width: 2000px) {
        width: 100%;
        margin-right: 0rem;
    }
}

.header-badge {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    .badge-cmp, .tag-cmp {
        padding: 1rem 2rem;
        border: var(--white-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;
        background: rgba(228, 228, 228, 0.1);
        box-shadow: 0 8px 32px 0 rgba(55, 91, 128, 0.5);
        backdrop-filter: blur( 10px );
        -webkit-backdrop-filter: blur( 10px );
        // border: 1px solid rgba( 255, 255, 255, 0.18 );

    }

    .tag-cmp {
        flex-direction: column;
        margin-top: 3rem;

        p {
            width: 100%;
            text-align: right;
            text-transform: uppercase;
        }
    }

    span {
        font-size: 2.5rem;

        @media screen and (min-width: 2000px) {
            font-size: 5rem;
        }
    }

    @media screen and (max-width: 1200px) {
        justify-content: flex-start;
        align-items: flex-start;
    }
}

.header-circles {
    flex: .75;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
    margin: auto;
    margin-left: 1rem;

    div {
        width: 100px;
        height: 100px;
        background: rgba( 255, 255, 255, 0.9 );
        box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
        backdrop-filter: blur( 10px );
        -webkit-backdrop-filter: blur( 10px );
        border-radius: 50%;
        border: 1px solid rgba( 255, 255, 255, 0.18 );

        img {
            width: 60%;
            height: 60%;
        }
    }

    div:nth-child(1) {
        width: 100px;
        height: 100px;
    }
    div:nth-child(2) {
        margin: 1.75rem;
        width: 150px;
        height: 150px;
    }
    div:nth-child(3) {
        width: 70px;
        height: 70px;
    }

    @media screen and (min-width: 2000px) {
        div:nth-child(1) {
            width: 200px;
            height: 200px;
        }
        div:nth-child(2) {
            margin: 1.75rem;
            width: 400px;
            height: 400px;
        }
        div:nth-child(3) {
            width: 160px;
            height: 160px;
        }
    }

    @media screen and (max-width: 1200px) {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 0;

        div {
            margin: 1rem;
        }
    }
}

.header-img {
    flex: 1;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: relative;

    img {
        width: 100%;
        object-fit: contain;
        z-index: 1;
    }

    .overlay-circle {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        height: 90%;
    }

    @media screen and (max-width: 1200px) {
        margin: 2rem 0rem ;
    }
}
