.navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    // background: rgba(130, 130, 130, 0.01);
   background: transparent;
    
    position: fixed;
    z-index: 2;
    height: 75px;
}

.blur {
    transition: all .15s ease;
    background: rgba(130, 130, 130, 0.02) !important;
    backdrop-filter: blur(4px) !important;
    -webkit-backdrop-filter: blur(4px)  !important;
    border-bottom: 1px solid var(--gray-color) !important;
}

.navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    img{
        width: 100%;
        height: 70px;

        @media screen and (min-width: 2000px) {
            width: 180px;
            height: 40px;
        }
    }
}

.navbar-links {
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
    list-style: none;

    li {
        margin: 0rem 2rem;
        cursor: pointer;
        flex-direction: column;

        div{
            width: 5px;
            height: 5px;
            background: transparent;
            border-radius: 50%;
            margin: auto;
            margin-bottom: 4px;
        }

        a{
            color: var(--gray-color);
            text-decoration: none;
            flex-direction: column;
            text-transform: uppercase;
            font-weight: 500;
            transition: all 0.3s ease-in-out;

            &:hover {
                color: var(--secondary-color);
            }
        }

        &:hover {
            div {
                background: var(--secondary-color);
            }
        }
    }

    @media screen and (max-width: 900px) {
        display: none;
    }
}

.navbar-menu {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--secondary-color);

    svg {
        width: 70%;
        height: 70%;
        color: var(--white-color);
    }

    div {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        
        z-index: 5;
        padding: 1rem;
        width: 80%;
        height: 100vh;
        box-shadow: 0 0 20px rgba(168, 168, 168, .15);

        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;
        
        background: url('../../assets/bgWhite.png');
        background-color: var(--white-color);
        background-size: cover;
        background-repeat: repeat;

        svg {
            width: 35px;
            height: 35px;
            color: var(--secondary-color);
            margin: .5rem 1rem;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
        }

        li {
            margin: 1rem;

            a {
                color: var(--gray-color);
                font-size: 1rem;
            }
        }
    }

    @media screen and (min-width: 900px) {
        display: none;
    }

}

.switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 22px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 30px;
    width: 30px;
    left: 0px;
    bottom: 4px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    box-shadow: 0 0px 15px #2020203d;
    background: white url('https://i.ibb.co/7JfqXxB/sunny.png');
    background-repeat: no-repeat;
    background-position: center;
    border: none;
  }
  
  input:checked + .slider {
    background-color: #535353;

  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #535353;
    border: 1px solid white;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(24px);
    -ms-transform: translateX(24px);
    transform: translateX(24px);
    background: rgb(0, 0, 0) url('https://i.ibb.co/FxzBYR9/night.png');
    background-repeat: no-repeat;
    background-position: center;
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
    width: 48px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }